import { Routes, RouterModule } from '@angular/router';
import { ModuleWithProviders } from '@angular/core';
import { LifeSycleQueryComponent } from './life-cycle-query.component';
import { ListAfterEnterThePoolComponent } from 'app/pages/tzb/custom/life-cycle/list-after-enter-the-pool/list-after-enter-the-pool.component';
import { CustomerAllocationPoolComponent } from 'app/pages/tzb/custom/life-cycle/customer-allocation-pool/customer-allocation-poolcomponent';
import { CustomerContactRecordComponent } from 'app/pages/tzb/custom/life-cycle/customer-contact-record/customer-contact-recordcomponent';
import { ContactProgressEnquiryComponent } from 'app/pages/tzb/custom/life-cycle/contact-progress-enquiry/contact-progress-enquiry.component';
import { ContactEffectivenessEnquiryComponent } from 'app/pages/tzb/custom/life-cycle/contact-effectiveness-enquiry/contact-effectiveness-enquiry.component';

export const routes: Routes = [
    {
        path: '',
        component: LifeSycleQueryComponent,
        children: [
            { path: '', redirectTo: 'list-after-enter-the-pool', pathMatch: 'full' },
            {
                path: 'list-after-enter-the-pool', component: ListAfterEnterThePoolComponent,// 入池候选清单
            },
            {
                path: 'customer-allocation-pool', component: CustomerAllocationPoolComponent,//客户分配池
            },
            {
                path: 'customer-contact-record', component: CustomerContactRecordComponent,//客户联系记录
            },
            {
                path: 'contact-progress-enquiry', component: ContactProgressEnquiryComponent,//客户进展查询
            },
            {
                path: 'contact-effectiveness-enquiry', component: ContactEffectivenessEnquiryComponent,//客户成效查询
            }
        ]
    }
]

export const LifeCycleQueryRouting: ModuleWithProviders = RouterModule.forChild(routes);
