import { Component, OnInit, Output, Input, EventEmitter } from '@angular/core';
import { FormGroup, FormBuilder, Validators } from "@angular/forms";
import { Message } from "primeng/primeng";
import { TZB_CUS_UPLOAD, TZB_CUS_DOWNLOAD, TZB_HTTP_CUS2 } from 'app/@uisftech/common/service/http.constant';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
import { MarketingHttpService } from 'app/pages/tzb/custom/marketing/http/marketing.http.service';
import { API } from 'app/pages/tzb/custom/marketing/http/marketing.constant';
import * as $ from 'jquery';

@Component({
	selector: 'pool-batch-file-import',
	templateUrl: './pool-batch-file-import.component.html',
	styleUrls: ['./pool-batch-file-import.component.scss'],
	providers: [MarketingHttpService]
})
export class PoolBatchFileImportComponent implements OnInit {
	@Output() outValue = new EventEmitter();
	@Input('in-value') inValue: any;
	UPLOAD: string = TZB_CUS_UPLOAD;
	DOWNLOAD: string = TZB_CUS_DOWNLOAD;
	files: any = [];
	batchFileResultDisplay: boolean = false;
	TZB_HTTP_CUS2 = TZB_HTTP_CUS2;
	showDisabled: boolean = false;//可编辑
	msgs: Message[] = [];
	loadObj: any = {};
	dtId: string;//模板编号
	examplesList: any = [];
    batchFileInvalue:any = {};
	constructor(
		public commfunc: Commfunc,
		public marketingHttpService: MarketingHttpService,
	) {

	}

	ngOnInit() {
		debugger
		console.log(this.inValue)

		this.findDocumentTemplate();
	}

	findDocumentTemplate() {
		let param = {
			fileTemplateSub: '',
			fileTemplateCate: '23',//主类型是根据任务类型进行变化
		}
		this.marketingHttpService.findDocumentTemplate(param).subscribe(data => {
			if (data.returnCode.code == 'success') {
				this.dtId = data.templateModleList[0].dtId;
			} else {
				this.msgs = [];
				this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
			}
		}, error => {
			this.msgs = [];
			this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败' });
		})
	}

	doConfirm() {
		this.outValue.emit();
	}

	importResult() {
		this.batchFileResultDisplay = true;
	}

	close() {
		this.batchFileResultDisplay = false;
	}

	onBeforeSend() {

	}

	//文件导入-导入按钮
	loadAfter(event) {
		this.files = [];
		for (let file of event.files) {
            this.files.push(file)
        }
		if (event.xhr.response) {
			if (JSON.parse(event.xhr.response).success == false) {
				this.msgs = [];
				this.msgs.push({ severity: 'error', summary: '提示', detail: JSON.parse(event.xhr.response).errorMsg });
				return
			} else {
				let arr = {};
				arr['fileName'] = JSON.parse(event.xhr.response)[0].fileName;
				arr['fileUrl'] = JSON.parse(event.xhr.response)[0].fileUrl;
				this.loadObj = arr;
				let param = {
					dtId: this.dtId,
					marketingCampaignId: this.inValue.marketingCampaignId,
					deFilename: JSON.parse(event.xhr.response)[0].fileName,
					deFileurl: JSON.parse(event.xhr.response)[0].fileUrl
				}
				let params = this.commfunc.handleParams(param)
				let TZB_HTTP_CUS2 = this.TZB_HTTP_CUS2 + API.doucumentDataDeal;
				let _self = this;
				$.ajax({
					url: TZB_HTTP_CUS2,
					type: 'POST',
					data: JSON.stringify(params),
					async: false,
					headers: {
						'x-ajax': '1',
						'Content-Type': 'application/json'
					},
					dataType: 'json',
					success: function (data) {
						if (data.returnCode.code == 'success') {
							_self.showDisabled = true;
							_self.findDocumentExamples(data.deId, data.contentId);
						} else {
							_self.showDisabled = false;
							_self.msgs = [];
							_self.msgs.push({ severity: 'error', summary: 'Error Message', detail: data.returnCode.message });
						}
					},
					error: function (err) {
						console.log(err);
					}
				})
			}
		}
	}

	//文件导入回显列表
	findDocumentExamples(item, items) {
		debugger
		this.commfunc.setSessionDataCH('deId', item);
		if (items != '') {
			this.commfunc.setSessionDataCH('contentId', items);
		}
		let param = {
			deId: item
		}
		this.marketingHttpService.findDocumentExamples(param).subscribe(data => {
			if (data.returnCode.code == "success") {
				this.batchFileInvalue = {
					taskFlag: this.inValue.taskFlag,
					marketingCampaignId: this.inValue.marketingCampaignId,
					receivePersonOrg: this.inValue.receivePersonOrg,
					marketTopic: this.inValue.marketTopic,
					instancyDegree: this.inValue.instancyDegree,// 紧急程度
					suggestContactCycle: this.inValue.suggestContactCycle,// 建议联系频度
					dt: this.commfunc.transDateN(new Date()),
					custSource: this.inValue.custSource,
					dataSource: this.inValue.dataSource,
				}
				// 延时
				setTimeout(() => {
					this.outValue.emit({batchFileResultInvalue:this.batchFileInvalue})
                }, 2000)
			} else {
				this.msgs = [];
				this.msgs = [{ severity: 'error', summary: '提示', detail: data.returnCode.message }];
			}
		}, error => {
			this.msgs = [];
			this.msgs = [{ severity: 'error', summary: '提示', detail: '调用服务失败' }];
		})

	}

	closebatch() {
		this.outValue.emit(false);
	}

	// 模板下载
	modelDownload() {
		this.marketingHttpService.documentDownload({ dtId: this.dtId }).subscribe(data => {
			if (data.returnCode.code == "success") {
				window.open(this.DOWNLOAD + "?fileName=" + encodeURI(data.fileName) + "&fileUrl=" + data.fileUrl, "_self")
			} else {
				this.msgs = [];
				this.msgs = [{ severity: 'error', summary: '提示', detail: data.returnCode.message }];
			}
		})
	}

	//按钮权限
	permissionCheck(id) {
		return this.commfunc.permissionCheck(id);
	}

}