import { Component, OnInit, Output, Input, EventEmitter } from '@angular/core';
import { FormGroup, FormBuilder, Validators } from "@angular/forms";
import { Message } from "primeng/primeng";
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
import { tableMessage } from "app/@uisftech/common/constant/config";
import { CusPoolHttpService } from 'app/pages/tzb/custom/http/custom-pool/custom-pool.http.service';

@Component({
    selector: 'pool-advance-list',
    templateUrl: './pool-advance-list.component.html',
    styleUrls: ['./pool-advance-list.component.scss'],
})
export class PoolAdvanceListComponent implements OnInit {
    @Output() outValue = new EventEmitter();
    @Input('in-value') inValue: any;
    tableMesg: any = tableMessage;
    tableList: any = [];
    successNum: any;
    failNum: any;
    msgs: Message[] = [];
    custList: any = [];
    constructor(
        public commfunc: Commfunc,
        public cusPoolHttpService: CusPoolHttpService,
    ) {

    }

    ngOnInit() {
        let param = JSON.parse(this.inValue.poolMarketingCampaignIdResult);
        this.tableList = param.loseList == null ? [] : param.loseList;
        this.successNum = param.sucessNum;
        this.failNum = param.loseList.length;
    }

    //按钮权限
    permissionCheck(id) {
        return this.commfunc.permissionCheck(id);
    }

}