import { Component, OnInit, Output, Input, EventEmitter } from '@angular/core';
import { FormGroup, FormBuilder, Validators } from "@angular/forms";
import { Message } from "primeng/primeng";
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
import { tableMessage } from "app/@uisftech/common/constant/config";
import { CusPoolHttpService } from 'app/pages/tzb/custom/http/custom-pool/custom-pool.http.service';

@Component({
    selector: 'pool-advanced-search-import-result',
    templateUrl: './pool-advanced-search-import-result.component.html',
    styleUrls: ['./pool-advanced-search-import-result.component.scss'],
})
export class PoolAdvancedSearchImportResultComponent implements OnInit {
    @Output() outValue = new EventEmitter();
    @Input('in-value') inValue: any;
    tableMesg: any = tableMessage;
    tableList: any = [];
    successNum: any;
    failNum: any;
    msgs: Message[] = [];
    custList: any = [];
    userId: string;
    orgId: string;
    user1: any;
    user: any;
    
    constructor(
        public commfunc: Commfunc,
        public cusPoolHttpService: CusPoolHttpService,
    ) {
        this.user1 = JSON.parse(this.commfunc.getSessionData('commonHeader'));
        if (this.user1.userId) {
            this.userId = this.user1.userId;
        }
        if (this.user1.orgId) {
            this.orgId = this.user1.orgId;
        }
    }

    ngOnInit() {
        debugger
        console.log(this.inValue)
        let param = {
            marketingCampaignId: this.inValue.marketingCampaignId,
        }
        let inputParams = {
            inputParams: param,
        }
        this.cusPoolHttpService.queryCustPoolPreBatchTotal(inputParams).subscribe(data => {
            if (data.returnCode.code == 'success') {
                if (data.outputParams) {
                    this.tableList = data.outputParams.resultList == null ? [] : data.outputParams.resultList
                    this.successNum = data.outputParams.successNum;
                    this.failNum = data.outputParams.failNum;
                    if (parseInt(this.successNum) > 0) {
                       
                        this.updateList();
                    }
                } else {
                    this.tableList = [];
                    this.successNum = null;
                    this.failNum = null;
                }
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务失败！" });
        });
    }

    querySuccessList() {
        this.custList = [];
        let tableArr = [];
        let param = {
            marketingCampaignId: this.inValue.marketingCampaignId,
            dedStatus: '0',
        }
        let inputParams = {
            inputParams: param,
        }
        this.cusPoolHttpService.queryCustPoolPre(inputParams).subscribe(data => {
            if (data.returnCode.code == 'success') {
                if (data.outputParams) {
                    tableArr = data.outputParams.resultList == null ? [] : data.outputParams.resultList
                    if (tableArr.length > 0) {
                        tableArr.forEach(element => {
                            this.custList.push({ custId: element.custId, dedId: element.dedId })
                        });
                    }
                    if (this.inValue.taskFlag && this.inValue.taskFlag == '1') {
                        this.checkAddRule();
                    } else {
                        this.toPool();
                    }
                }
              
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务失败！" });
        });
    }

    // 修改数据
    updateList() {
        let modifyParam = {
            operType: '2',
            marketingCampaignId: this.inValue.marketingCampaignId,
            instancyDegree: this.inValue.instancyDegree,// 紧急程度
            suggestContactCycle: this.inValue.suggestContactCycle,// 建议联系频度
            receivePersonOrg: this.inValue.receivePersonOrg,// 接收机构
            marketTopic: this.inValue.marketTopic,
            dt: this.commfunc.transDateN(new Date()),
            custSource: this.inValue.custSource,
            dataSource: this.inValue.dataSource,
            agent: this.userId,
            agentOrg: this.orgId,
            // inventoryStatus: '1',
        }
        let param = {
            inputParams: modifyParam
        }
        this.cusPoolHttpService.modifyCustPoolPre(param).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.querySuccessList();
                
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败' });
        })
    }

    // 直接入池  
    toPool() {
        let param = {
            custInfoList: this.custList,
            marketingCampaignId: this.inValue.marketingCampaignId,
            suggestContactCycle: this.inValue.suggestContactCycle,
            receivePersonOrg: this.inValue.receivePersonOrg,// 接收机构
            marketTopic: this.inValue.marketTopic,
            instancyDegree: this.inValue.instancyDegree,// 紧急程度
            dataDt: this.commfunc.transDateN(new Date()),
            custSource: this.inValue.custSource,
            custSearchFlag: '1',
            taskFlag: '0',
            dataSource: this.inValue.dataSource,
        }
        this.cusPoolHttpService.custSearchToCustPool(param).subscribe(data => {
            if (data.returnCode.code == 'success') {

            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务失败！" });
        });

    }

    // 接收机构校验
    checkAddRule() {
        console.log('接收机构校验')
        let param = {
            custList: this.custList,
            marketingCampaignId: this.inValue.marketingCampaignId,
            receivePersonOrg: this.inValue.receivePersonOrg,
            marketTopic: this.inValue.marketTopic,
        }
        this.cusPoolHttpService.checkAddRule(param).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.startManageProcess();
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务失败！" });
        });
    }

    // 生成待办任务
    startManageProcess() {
        console.log('生成待办任务')
        let param = {
            marketingCampaignId: this.inValue.marketingCampaignId,
        }
        this.cusPoolHttpService.startManageProcess(param).subscribe(data => {
            if (data.returnCode.code == 'success') {

            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务失败！" });
        });
    }

    doConfirm() {
        this.outValue.emit();
    }

    //按钮权限
    permissionCheck(id) {
        return this.commfunc.permissionCheck(id);
    }

}