import { Component, OnInit, Output, Input, EventEmitter } from '@angular/core';
import { FormGroup, FormBuilder, Validators } from "@angular/forms";
import { Message } from "primeng/primeng";
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
import { CusPoolHttpService } from 'app/pages/tzb/custom/http/custom-pool/custom-pool.http.service';
@Component({
    selector: 'pool-optional-filed',
    templateUrl: './pool-optional-filed.component.html',
    styleUrls: ['./pool-optional-filed.component.scss'],
})
export class PoolOptionalFiledComponent implements OnInit {
    @Output() outValue = new EventEmitter();
    @Input('in-value') inValue: any;
    msgs: Message[];
    publicAttrList: any = [];
    selfDefineAttrList: any = [];
    selfDefineChange: any = [];
    publicChange: any = [];
    user: any;
    userId: string;

    constructor(
        public commfunc: Commfunc,
        public cusPoolHttpService: CusPoolHttpService,
    ) {

    }

    ngOnInit() {
        this.user = JSON.parse(this.commfunc.getSessionData('commonHeader'));
        if (this.user.userId) {
            this.userId = this.user.userId;
        }
        let inputParams = {
            inputParams: {}
        }
        this.cusPoolHttpService.custPoolOptionField(inputParams).subscribe(data => {
            if (data.returnCode.code == 'success') {
                if (data.outputParams) {
                    console.log(data.outputParams.publicAttrList)
                    console.log(data.outputParams.selfDefineAttrList)
                    this.publicAttrList = data.outputParams.publicAttrList == null ? [] : data.outputParams.publicAttrList;
                    this.selfDefineAttrList = data.outputParams.selfDefineAttrList == null ? [] : data.outputParams.selfDefineAttrList;
                    this.publicChange = [];
                    this.publicAttrList.forEach((item) => {
                      if (item.isSelect == '1') {
                        item.checked = true;
                        this.publicChange.push(item);
                      } else {
                        item.checked = false;
                      }
                    });
                    this.selfDefineChange = [];
                    this.selfDefineAttrList.forEach((item) => {
                      if (item.isSelect == '1') {
                        item.checked = true;
                        this.selfDefineChange.push(item);
                      } else {
                        item.checked = false;
                      }
                    });
                } else {
                }
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
            }

        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败' });
        })
    }

    publicCheckAdd(item){
        if (item.checked) {
            this.publicChange.push(item);
          } else {
            this.publicChange.forEach((tep, i) => {
              if (item.indicatorId == tep.indicatorId) {
                this.publicChange.splice(i, 1)
              }
            });
          }
    }

    selfDefineCheckAdd(item){
        if (item.checked) {
            this.selfDefineChange.push(item);
          } else {
            this.selfDefineChange.forEach((tep, i) => {
              if (item.indicatorId == tep.indicatorId) {
                this.selfDefineChange.splice(i, 1)
              }
            });
          }
    }
    
    doConfirm() {
        let arr = [];
        this.publicChange.forEach(element => {
            arr.push({indicatorId:element.indicatorId})
          });
          this.selfDefineChange.forEach(element => {
            arr.push({indicatorId:element.indicatorId})
          });
        let InParam = {
            operType:'1',
            sceneType:'005',
            userId:this.userId,
            indicatorIdGroup: JSON.stringify(arr),
        }
        let param = {
            inputParams: InParam
        }
        this.cusPoolHttpService.modifySelfDefineIndicator(param).subscribe(data => {
            if (data.returnCode.code == 'success') {
                 this.outValue.emit();
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务失败！" });
        });
      
    }

    //按钮权限
    permissionCheck(id) {
        return this.commfunc.permissionCheck(id);
    }

}