import { Component, OnInit } from '@angular/core';
import { FormGroup, FormBuilder, Validators } from "@angular/forms";
//import { MessageService } from "primeng/components/common/messageservice";
//import { Customtools } from "app/pages/tzb/custom/http/customUtil";
import { Commfunc } from "app/@uisftech/common/service/commonFun";
import { specialSymbol, checkNumber, checkCard } from "app/@uisftech/common/service/validator";
// import { CusListManageHttpService } from "app/pages/tzb/custom/http/custom-list-manage/custom-list-manage.http.service";
import { Message } from "primeng/primeng";
import { Router } from '@angular/router'

@Component({
    selector: 'life-cycle-query',
    templateUrl: './life-cycle-query.component.html',
    styleUrls: ['./life-cycle-query.component.scss'],
})
export class LifeSycleQueryComponent implements OnInit {
    cars: any = [];
    carsTemp: any;
    showClass: number = 0;

    constructor(
        public commfunc: Commfunc,
        public router: Router,
    ) {

    }

    ngOnInit() {
        this.carsTemp = [
            {
                title: '入池候选清单',
                link: 'list-after-enter-the-pool',
                id: 'SID00021_P001',
                flag: 0
            },
            {
                title: '客户分类池',
                link: 'customer-allocation-pool',
                id: 'SID00021_P002',
                flag: 1
            },
            {
                title: '客户联系记录',
                link: 'customer-contact-record',
                id: 'SID00021_P003',
                flag: 2
            },
            {
                title: '客户进展查询',
                link: 'contact-progress-enquiry',
                id: 'SID00021_P004',
                flag: 3
            },
            {
                title: '客户成效查询',
                link: 'contact-effectiveness-enquiry',
                id: 'SID00021_P005',
                flag: 4
            }
        ];
        this.carsTemp.forEach((element, i) => {//根据权限判断显示tab页
            if (this.permissionCheck(element.id)) {
                this.cars.push(element);
            }
        });
        this.showHideBriefInit();
    }

    showHideBriefInit() {
        let s = window.location.href;
        let stu_1 = this.commfunc.permissionCheck('SID00021_P001'); // 入池候选清单
        let stu_2 = this.commfunc.permissionCheck('SID00021_P002'); // 客户分配池
        let stu_3 = this.commfunc.permissionCheck('SID00021_P003'); // 客户联系记录
        let stu_4 = this.commfunc.permissionCheck('SID00021_P004'); // 客户进展查询
        let stu_5 = this.commfunc.permissionCheck('SID00021_P005'); // 客户成效查询

        if (s.includes('/pages/tzb/custom/life-cycle/life-cycle-query/list-after-enter-the-pool') && stu_1) {
            this.chexk(0);
        } else if (s.includes('/pages/tzb/custom/life-cycle/life-cycle-query/customer-allocation-pool') && stu_2) {
            this.chexk(1);
        } else if (s.includes('/pages/tzb/custom/life-cycle/life-cycle-query/customer-contact-record') && stu_3) {
            this.chexk(2);
        } else if (s.includes('/pages/tzb/custom/life-cycle/life-cycle-query/contact-progress-enquiry') && stu_4) {
            this.chexk(3);
        } else if (s.includes('/pages/tzb/custom/life-cycle/life-cycle-query/contact-effectiveness-enquiry') && stu_5) {
            this.chexk(4);
        } else if ('/pages/tzb/custom/life-cycle/life-cycle-query/list-after-enter-the-pool') {
            this.chexk(this.cars[0].flag);
        }
    }

    chexk(num) {
        this.showClass = num;
        this.router.navigate(['/pages/tzb/custom/life-cycle/life-cycle-query/' + this.cars[num].link])
    }


    // 切换
    showHideBrief1(index) {
        this.showClass = index;
    }

    //按钮权限
    permissionCheck(id) {
        return this.commfunc.permissionCheck(id);
    }

}