import { Component, OnInit, Output, Input, EventEmitter } from '@angular/core';
import { FormGroup, FormBuilder, Validators } from "@angular/forms";
import { Message } from "primeng/primeng";
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
import { tableMessage } from "app/@uisftech/common/constant/config";
import { CusPoolHttpService } from 'app/pages/tzb/custom/http/custom-pool/custom-pool.http.service';

@Component({
    selector: 'big-data-plat-pool-result',
    templateUrl: './big-data-plat-pool-result.component.html',
    styleUrls: ['./big-data-plat-pool-result.component.scss'],
})
export class BigDataPlatPoolResultComponent implements OnInit {
    @Output() outValue = new EventEmitter();
    @Input('in-value') inValue: any;
    tableMesg: any = tableMessage;
    tableList: any = [];
    successNum: any;
    failNum: any;
    msgs: Message[] = [];
    custList: any = [];
    constructor(
        public commfunc: Commfunc,
        public cusPoolHttpService: CusPoolHttpService,
    ) {

    }

    ngOnInit() {
        if (this.inValue) {
            this.tableList = this.inValue.loseList == null ? [] : this.inValue.loseList;
            this.successNum = this.inValue.sucessNum;
            this.failNum = this.inValue.loseList.length;
        } else {
            this.tableList = [];
            this.successNum = null;
            this.failNum = null;
        }
    }

    //按钮权限
    permissionCheck(id) {
        return this.commfunc.permissionCheck(id);
    }

}