import { Component, OnInit, Output, EventEmitter, Input } from '@angular/core';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
import { Message } from "primeng/primeng";
import { tableMessage } from "app/@uisftech/common/constant/config";
import { CALENDAR_CN } from 'app/pages/tzb/workflow/activiti-work-flow/common/constant/config';
import { Router } from '@angular/router';
import { CustomerAddBean } from 'app/pages/tzb/custom/life-cycle/customer-add/bean/customer-add.bean';
import { MarketingHttpService } from 'app/pages/tzb/custom/marketing/http/marketing.http.service';
import { BasicElementBean } from 'app/pages/tzb/custom/marketing/marketing-activite/basic-element/bean/basic-element.bean';
import * as _ from 'lodash';
import { CusPoolHttpService } from 'app/pages/tzb/custom/http/custom-pool/custom-pool.http.service';

@Component({
    selector: 'customer-add',
    templateUrl: './custome-add.component.html',
    styleUrls: ['./custome-add.component.scss'],
    providers: [MarketingHttpService]
})

export class customerddComponent implements OnInit {

    @Input('in-value') inValue: any;
    @Output() outValue = new EventEmitter();
    tableMesg: any = tableMessage;
    msgs: Message[] = [];
    msgs2: Message[] = [];
    ch: any = CALENDAR_CN;

    // commonHeader: any;
    tableData: any = [];
    dataSource: string;
    customerSourceList: any[] = [];
    dataSourceFlag: boolean = false;
    dataDate: Date = new Date();
    customerSource: string = '';
    Property1: boolean = true;
    Property2: boolean = true;
    Property3: boolean = true;
    bigDataScreen: boolean = false;
    bigDataPlantformDisplay: boolean = false;
    selectedCities: string;
    nextDisplay: boolean = true;
    saveDisplay: boolean = false;
    bean: CustomerAddBean = new CustomerAddBean();
    custSource: any = [];
    instancyDegree: any = [];
    suggestContactCycle: any = [];
    marketTopic: any = [];
    orgTreeDisplay: boolean = false;
    dt: Date = null;
    receivePersonOrgName: string;

    user: any;
    user2: any;
    userId: string;
    orgId: string;
    userName: string;
    orgName: string;
    permission: string;
    op: string;

    receivePersonOrg: string;
    batchFileDisplay: boolean = false;
    marketingCampaignId: string;
    flagCopy: string = '1';
    batchFileInvalue: any;

    fileDisplay: boolean = false;
    inValue2: any;
    custList: any = [];
    dataObj = {};//接收表格中的数据
    dataObjList: any = [];
    tableList: any = [];
    isCommmit: boolean = false;
    bigData: any = [];
    code: any;

    bigDataPlatPoolDisplay: boolean = false;
    inValue3: any;

    constructor(
        public commfunc: Commfunc,
        public router: Router,
        public marketingHttpService: MarketingHttpService,
        public cusPoolHttpService: CusPoolHttpService,
    ) {
        this.codeVal();
        this.instancyDegree = this.code['PoolinstancyDegree'];
        this.suggestContactCycle = this.code['PoolSuggestContactCycle'];
        this.marketTopic = this.code['PoolMarketTopic'];
        // this.instancyDegree = [
        //     { label: '请选择', value: '' },
        //     { label: '一星', value: '1' },
        //     { label: '二星', value: '2' },
        //     { label: '三星', value: '3' },
        //     { label: '四星', value: '4' },
        //     { label: '五星', value: '5' },
        // ]
        // this.suggestContactCycle = [
        //     { label: '请选择', value: '' },
        //     { label: '1个月', value: '01' },
        //     { label: '3个月', value: '02' },
        //     { label: '6个月', value: '03' },
        //     { label: '12个月', value: '04' },
        //     { label: '24个月', value: '05' },
        // ]
        // this.marketTopic = [
        //     { label: '请选择', value: '' },
        //     { label: '交叉销售', value: '1' },
        //     { label: '礼品登记', value: '2' },
        //     { label: '营销管理', value: '3' },
        // ];
    }

    //码值
    codeVal() {
        this.commfunc.codeValue();
        let ret = this.commfunc.codeDatas;
        return this.code = ret //code是自己声明的变量
    }   
        
    ngOnInit() {
        this.dt = new Date();
        this.user = JSON.parse(this.commfunc.getSessionData('commonHeader'));
        this.user2 = JSON.parse(this.commfunc.getSessionDataCH('chName'));
        this.preset();
    }

    preset() {
        // 当前登录用户
        if (this.user.orgId) {
            this.orgId = this.user.orgId;
        }
        if (this.user.userId) {
            this.userId = this.user.userId;
        }
        if (this.user2.userName) {
            this.userName = this.user2.userName;
        }
        if (this.user2.orgName) {
            this.orgName = this.user2.orgName;
        }

        // 当前登录用户岗位
        if (this.user.extParam.managePermission) {
            this.permission = this.user.extParam.managePermission;
        }
        if (this.user.extParam.operatingLevel) {
            this.op = this.user.extParam.operatingLevel;
        }

        // 客户经理
        if (this.permission == 'N') {
            this.custSource = [
                { label: '客户经理自定义', value: '4' }
            ];
            this.case('4');
        } else {
            if (this.permission == 'Y') {
                // 分支行管理岗
                if (this.op == 'SUB_BRANCH_DT' || this.op == 'SUB_BRANCH' || this.op == 'BRANCH' || this.op == 'BRANCH_DT') {
                    this.custSource = [
                        { label: '分支行制定', value: '3' },
                    ];
                    this.case('3');
                } else {
                    // 总行管理人员
                    this.custSource = [
                        { label: '大数据推送', value: '2' },
                        { label: '互联网获客', value: '1' },
                    ];
                    this.case('2');
                }
            }
        }

        // 营销活动编号
        if (this.inValue && this.inValue['marketingCampaignId']) {

        }
    }

    // 控制页面展示
    case(num) {
        this.dataSource = null;
        this.dataSourceFlag = false;
        this.bigDataScreen = false;
        this.Property1 = false;
        this.Property2 = false;
        this.Property3 = false;
        this.nextDisplay = false;
        this.saveDisplay = false;
        if (num == '1') { // 1-互联网获客
            this.bean.custSource = '1';
            this.dataSourceFlag = true;
            this.bigDataScreen = false;
            this.Property1 = false;
            this.Property2 = true;
            this.Property3 = false;
            this.nextDisplay = true;
            this.saveDisplay = false;
        } else if (num == '2') { // 2-大数据推送
            this.bean.custSource = '2';
            this.dataSourceFlag = false;
            this.bigDataScreen = true;
            this.Property1 = false;
            this.Property2 = false;
            this.Property3 = false;
            this.nextDisplay = true;
            this.saveDisplay = true;
            this.dataSource = '3';
            // this.getBigDataStatisticsList();
        } else if (num == '3') { // 3-分支行制定
            this.bean.custSource = '3';
            this.dataSourceFlag = true;
            this.bigDataScreen = false;
            this.Property1 = true;
            this.Property2 = true;
            this.Property3 = false;
            this.nextDisplay = true;
            this.saveDisplay = false;
        } else if (num == '4') { // 4-客户经理自定义
            this.bean.custSource = '4';
            this.dataSourceFlag = true;
            this.bigDataScreen = false;
            this.Property1 = true;
            this.Property2 = true;
            this.Property3 = false;
            this.nextDisplay = true;
            this.saveDisplay = false;
        }
    }

    // 更改客户来源
    customerSourceChange() {
        switch (this.bean.custSource) {
            case '1': // 1-互联网获客
                this.case('1');
                break;
            case '2': // 2-大数据推送
                this.case('2');
                break;
            case '3': // 3-分支行制定
                this.case('3');
                break;
            case '4': // 4-客户经理自定义
                this.case('4');
                break;
        }
    }

    doOprate(type) {
        // 校验
        if (this.bean.custSource == undefined || this.bean.custSource == null) {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '提示', detail: '客户来源不能为空！' }];
            return
        }
        if (this.bean.marketingContactTheme == undefined || this.bean.marketingContactTheme == null) {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '提示', detail: '营销主题不能为空！' }];
            return
        }
        if (this.receivePersonOrgName == undefined || this.receivePersonOrgName == null) {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '提示', detail: '接收机构不能为空！' }];
            return
        }
        if (this.bean.instancyDegree == undefined || this.bean.instancyDegree == null) {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '提示', detail: '紧急程度不能为空！' }];
            return
        }
        if (this.bean.suggestContactCycle == undefined || this.bean.suggestContactCycle == null) {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '提示', detail: '建议联系额度不能为空！' }];
            return
        }
        if (type == 'next') {
            this.next();
        }
        if (type == 'save') {
            this.save();
        }
    }

    next() {
        if (this.dataSource == undefined || this.dataSource == null) {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '提示', detail: '数据来源不能为空！' }];
            return
        }
        if (this.bean.custSource == '1') {  // 1-互联网获客
            if (this.dataSource == '2') {
                this.createMarketingCampaignNew();
            }
        } else if (this.bean.custSource == '2') { // 2-大数据推送
            this.dataSource = '3';
            this.createMarketingCampaignNew();
        } else if (this.bean.custSource == '3') { // 3-分支行制定
            if (this.dataSource == '1') {
                this.createMarketingCampaignNew();
            } else if (this.dataSource == '2') {
                this.createMarketingCampaignNew();
            }
        } else if (this.bean.custSource == '4') { // 4-客户经理自定义
            if (this.dataSource == '1') {
                this.router.navigate(['/pages/tzb/custom/custom-search']);
            } else if (this.dataSource == '2') {
                this.createMarketingCampaignNew();
            }
        }
    }

    // 创建营销活动
    createMarketingCampaignNew() {
        debugger
        let param = _.clone(this.bean);
        param['marketingCampaigntType'] = '23';
        param['dt'] = this.commfunc.transDateN(this.dt); // 数据日期
        param['fromDate'] = this.commfunc.transDateN(this.dt); // 营销活动时间-起
        param['thruDate'] = this.commfunc.transDateN(this.dt); // 营销活动时间-止
        param['marMonitoringTimeTo'] = this.commfunc.transDateN(this.dt); // 营销监控时间-止
        param['marMonitoringTimeFrom'] = this.commfunc.transDateN(this.dt); // 营销监控时间-起
        param['campaignName'] = '客户生命周期' + this.commfunc.getNowFormatDate();
        param['pushObject'] = this.bean.custSource;

        this.marketingHttpService.createMarketingCampaignNew(param).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.marketingCampaignId = data.marketingCampaignId;
                let paramSum = {
                    marketingCampaignId: data.marketingCampaignId,
                    suggestContactCycle: this.bean.suggestContactCycle,
                    receivePersonOrg: this.receivePersonOrg,
                    marketTopic: this.bean.marketingContactTheme,
                    instancyDegree: this.bean.instancyDegree,
                    dataDt: this.commfunc.transDateN(this.dt),
                    custSource: this.bean.custSource,
                    dataSource: this.dataSource,
                    taskFlag: '1',
                }
                //交叉销售
                setTimeout(() => {
                    if (this.flagCopy == '1') {//复制
                        this.roleClick(data.marketingCampaignId);
                    }
                }, 1500)

                // 判断客户来源
                if (this.dataSource == '1') {  // 高级搜索
                    let param = {
                        type: 'lifeCycle',
                        marketingCampaignId: this.marketingCampaignId,
                        suggestContactCycle: this.bean.suggestContactCycle,
                        receivePersonOrg: this.receivePersonOrg,
                        marketTopic: this.bean.marketingContactTheme,
                        instancyDegree: this.bean.instancyDegree,
                        dataDt: this.commfunc.transDateN(this.dt),
                        custSource: this.bean.custSource,
                        dataSource: this.dataSource,
                        taskFlag: '1',
                    }
                    this.router.navigate(['/pages/tzb/custom/custom-search', param]);
                } else if (this.dataSource == '2') {  // 批量导入
                    let param = {};
                    this.batchFileInvalue = paramSum;
                    this.outValue.emit({ batchFileInvalue: this.batchFileInvalue })
                    // this.batchFileDisplay = true;
                } else {  // 大数据推送
                    this.dataObjList = [];
                    this.selectedCities = null;
                    this.bigDataPlantformDisplay = true;
                    this.isCommmit = false;
                    this.getBigDataStatisticsList();
                }
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败' });
        })
    }

    //创建角色
    roleClick(marketingCampaignId) {
        let param = {
            marketingCampaignId: marketingCampaignId,
            marketingCampaigRole: '1',
            userId: this.userId,
            orgId: this.orgId,
        }
        this.marketingHttpService.createMarketingCampaignRole(param).subscribe(data => {
            if (data.returnCode.code == 'success') {
                // this.msgs = [];
                // this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
            }

        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败' });
        })

    }

    // 大数据平台筛选
    getBigDataStatisticsList() {
        let param = {
            inputParams: {
                dt: '',
            }
        }
        this.cusPoolHttpService.queryBdCustPoolPreBatchTotal(param).subscribe(data => {
            if (data.returnCode.code == "success") {
                this.tableData = data.outputParams.resultList == null ? [] : data.outputParams.resultList;
            } else {
                this.msgs = [];
                this.msgs = [{ severity: 'error', summary: '提示', detail: data.returnCode.message }];
            }
        }, error => {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '提示', detail: '调用服务失败' }];
        })
    }

    //大数据平台筛选详情
    toDetail(item) {
        this.fileDisplay = true;
        this.inValue2 = { modelNo: item.modelNo };
    }

    // 保存
    save() {
        if (this.dataObjList.length == 0) {
            this.msgs2 = [];
            this.msgs2.push({ severity: 'info', summary: '提示', detail: '请选择一条数据' });
            return
        } else {
            this.isCommmit = true;
            let param = {};
            param['marketingCampaignId'] = this.marketingCampaignId;
            param['pushDate'] = this.dataObj['dt'];
            param['marketingObjectType'] = '23';
            this.marketingHttpService.createCampaignCust(param).subscribe(data => {
                if (data.returnCode.code == 'success') {
                    this.query();
                } else {
                    this.isCommmit = false;
                    this.msgs2 = [];
                    this.msgs2.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
                }
            }, error => {
                this.isCommmit = false;
                this.msgs2 = [];
                this.msgs2.push({ severity: 'error', summary: '提示', detail: '调用服务失败' });
            })
        }
    }

    // 查询
    query() {
        let param = {
            inputParams: {
                modelNo: this.selectedCities,
                pageNum: 1,
                pageSize: 999,
            }
        }
        this.cusPoolHttpService.queryBdCustPoolPre(param).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.bigData = data.outputParams.resultList == null ? [] : data.outputParams.resultList;
                this.toExam();
            } else {
                this.isCommmit = false;
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
            }
        }, error => {
            this.isCommmit = false;
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务失败！" });
        });
    }

    // 推待办
    toExam() {
        let param = {
            custInfoList: this.bigData,
            marketingCampaignId: this.marketingCampaignId,
            suggestContactCycle: this.bean.suggestContactCycle,
            receivePersonOrg: this.receivePersonOrg,// 接收机构
            marketTopic: this.bean.marketingContactTheme,
            instancyDegree: this.bean.instancyDegree,// 紧急程度
            dataDt: this.commfunc.transDateN(this.dt),
            custSource: this.bean.custSource,
            custSearchFlag: '1',
            taskFlag:'1',
            dataSource: this.dataSource,
        }
        this.cusPoolHttpService.custSearchToCustPool(param).subscribe(data => {
            if (data.returnCode.code == 'success') {
                //  修改入选池清单
                let modifyParam = {
                    operType: '2',
                    marketingCampaignId: this.marketingCampaignId,
                    instancyDegree: this.bean.instancyDegree,// 紧急程度
                    suggestContactCycle: this.bean.suggestContactCycle,// 建议联系频度
                    // receivePersonOrg: this.bean.receivePersonOrg,// 接收机构
                    marketTopic: this.bean.marketingContactTheme,
                    dt: this.commfunc.transDateN(this.dt),
                    custSource: this.bean.custSource,
                    dataSource: this.dataSource,
                    agent: this.userId,
                    agentOrg: this.orgId,
                    // inventoryStatus: '1',
                }
                let arr = data.loseList == null ? [] : data.loseList;
                let result = {
                    sucessNum: data.sucessNum,
                    loseList: arr,
                }
                let param = {
                    inputParams: modifyParam
                }
                this.cusPoolHttpService.modifyCustPoolPre(param).subscribe(data => {
                    if (data.returnCode.code == 'success') {
                        this.isCommmit = false;
                        // this.outValue.emit(false);
                        this.inValue3 = result;
                        this.bigDataPlatPoolDisplay = true;
                    } else {
                        this.isCommmit = false;
                        this.msgs2 = [];
                        this.msgs2.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
                    }
                }, error => {
                    this.isCommmit = false;
                    this.msgs2 = [];
                    this.msgs2.push({ severity: 'error', summary: '提示', detail: '调用服务失败' });
                })
            } else {
                this.isCommmit = false;
                this.msgs2 = [];
                this.msgs2.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
            }
        }, error => {
            this.isCommmit = false;
            this.msgs2 = [];
            this.msgs2.push({ severity: 'error', summary: '提示', detail: "调用服务失败！" });
        });
    }

    // 接收机构校验
    checkAddRule() {
        let param = {
            custList: this.custList,
            marketingCampaignId: this.marketingCampaignId,
            receivePersonOrg: this.inValue.receivePersonOrg,
            marketTopic: this.inValue.marketTopic,
        }
        this.cusPoolHttpService.checkAddRule(param).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.startManageProcess();
            } else {
                this.msgs2 = [];
                this.msgs2.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
            }
        }, error => {
            this.msgs2 = [];
            this.msgs2.push({ severity: 'error', summary: '提示', detail: "调用服务失败！" });
        });
    }

    // 生成待办任务
    startManageProcess() {
        console.log('生成待办任务')
        let param = {
            marketingCampaignId: this.inValue.marketingCampaignId,
        }
        this.cusPoolHttpService.startManageProcess(param).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.bigDataPlantformDisplay = false;
                this.outValue.emit(false);
            } else {
                this.msgs2 = [];
                this.msgs2.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
            }
        }, error => {
            this.msgs2 = [];
            this.msgs2.push({ severity: 'error', summary: '提示', detail: "调用服务失败！" });
        });
    }

    return() {
        this.bigDataPlantformDisplay = false;
    }

    bigDataChoose(col) {

    }

    openTree() {
        this.orgTreeDisplay = true;
    }

    // 关闭模态框
    onHideClose(param) {
        let dataParam = param;
        if (dataParam == 'org') {
            this.orgTreeDisplay = false;
        }
    }

    close() {
        this.orgTreeDisplay = false;
    }

    // 关闭-机构树
    contacttreeCall(event) {
        this.orgTreeDisplay = false;
        this.receivePersonOrg = event.orgId;
        this.receivePersonOrgName = event.orgName;
    }

    outDisplayCall() {
        this.batchFileDisplay = false;
        this.outValue.emit(false);
    }

    //按钮权限
    permissionCheck(id) {
        return this.commfunc.permissionCheck(id);
    }

    //选择一条数据
    checkClick2(item) {
        this.dataObj = item;
        this.dataObjList = [];
        this.dataObjList.push(this.dataObj)
    }

    closeBigData() {
        this.bigDataPlatPoolDisplay = false;
        this.bigDataPlantformDisplay = false;
        this.outValue.emit({display:false,opType:'bigData'});
    }
}