import { Component, OnInit, Output, EventEmitter, Input } from '@angular/core';
import { CALENDAR_CN } from 'app/pages/tzb/custom/credit-ccm/common/http/credit-ccm.http.constant';
import { CustomerContactRecordDetailBean } from 'app/pages/tzb/custom/life-cycle/customer-contact-record-details/bean/customer-contact-record-detail.bean';
import { Message } from "primeng/primeng";
import { CusOperationHttpService } from 'app/pages/tzb/custom/http/custom-operation/custom-operation.http.service';
import { CustomerSource, ContactInformation, LinkageTheme, Priority, ProcessingResults } from 'app/pages/tzb/custom/custom-contact-new/custom-constant/custom-code-value';
import { CustomContactHistoryDetaileean } from 'app/pages/tzb/custom/custom-contact-new/custom-contact-history/bean/custom-contact-history.bean';

@Component({
    selector: 'customer-contact-record-details',
    templateUrl: './customer-contact-record-details.component.html',
    styleUrls: ['./customer-contact-record-details.component.scss']
})

export class CustomerContactRecordDetailsComponent implements OnInit {
    @Input() inValue: any;
    @Output() outValue = new EventEmitter();

    mapList: any = [
        { coreOrgName: '1' }
    ]
    deployTimeEnd: any;
    ch: any;
    deployTimeStart: any;
    valList: any;
    msgs: any;
    showFlagSubmit: boolean = true;

    customerSource: any = CustomerSource; //客户来源
    contactInformation: any = ContactInformation; //联系方式
    linkageTheme: any = LinkageTheme; //联系主题
    priority: any = Priority; //优先级
    processingResults: any = ProcessingResults; //优先级
    showContractNum: boolean = false;

    detaileean: CustomContactHistoryDetaileean = new CustomContactHistoryDetaileean()

    constructor(
        public httpService: CusOperationHttpService,
    ) { }

    ngOnInit() {
        console.log(this.inValue);

    }

    ngOnChanges() {
        console.log(this.inValue);
        this.query(this.inValue)
    }

    // 返回按钮
    reset() {
        this.outValue.emit({ dispaly: false })
    }

    query(col) {
        let param = {
            // workEffortId: col.communicationEventId, // 任务编号
            communicationEventId: col.communicationEventId, // 事件编号
        };
        this.httpService.queryConWorkEffortDetailInfo(param).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.detaileean = data;
                // 信贷类 02 11 12 14
                if (this.detaileean.marketingContactTheme == '02' || this.detaileean.marketingContactTheme == '11' || this.detaileean.marketingContactTheme == '12' || this.detaileean.marketingContactTheme == '14') {
                    this.showContractNum = true;
                }
                if (typeof this.detaileean.marketingContactTheme == "string" && this.detaileean.marketingContactTheme) {
                    this.detaileean['marketingContactTheme_'] = this.detaileean.marketingContactTheme.split(",")
                }else if(!this.detaileean.marketingContactTheme){
                    this.detaileean['marketingContactTheme_'] = '请选择'
                }
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务失败！" });
        })
    }
}
