import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { FormGroup, FormBuilder, Validators } from "@angular/forms";
import { Message } from "primeng/primeng";
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
import { CustomerAllocationPoolBean } from 'app/pages/tzb/custom/life-cycle/customer-allocation-pool/bean/customerAllocationPool.bean';
import { tableMessage } from "app/@uisftech/common/constant/config";
import { CusPoolHttpService } from 'app/pages/tzb/custom/http/custom-pool/custom-pool.http.service';
import { DecimalPipe } from '@angular/common';
import * as _ from 'lodash';
import { Router } from '@angular/router'
import { HttpService } from "app/@uisftech/common/service/http.service";
import * as $ from 'jquery';
import { API } from '../../http/custom-pool/custom-pool.http.constant';
import { TZB_HTTP_CUS } from "app/@uisftech/common/service/http.constant";
import { ContactInformation, TourcesPlanning, ProcessingResults, ProcessingStarts, LinkageTheme } from 'app/pages/tzb/custom/custom-contact-new/custom-constant/custom-code-value';
import { CusOperationHttpService } from 'app/pages/tzb/custom/http/custom-operation/custom-operation.http.service';

@Component({
    selector: 'pool-contact-list',
    templateUrl: './pool-contact-list.component.html',
    styleUrls: ['./pool-contact-list.component.scss'],
    providers: [DecimalPipe]
})
export class PoolContactListComponent implements OnInit {
    @Input('in-value') inValue: any;
    @Output() outValue = new EventEmitter();
    tableMesg: any = tableMessage;
    msgs: Message[] = [];
    tableList: any = [];
    code: any;
    custClassifyType: any = [];
    dealStatus: any = [];
    first: number = 0;
    pageNum: number = 1;
    pageSize: number = 10;
    totalNum: number = 0;
    suggestContactCycle: any = [];
    instancyDegree: any = [];

    custId: string;
    custName: string;
    linkageTheme: any = LinkageTheme; //联系主题
    contactInformationOption: any = ContactInformation; //联系方式
    tourcesPlanningOption: any = TourcesPlanning; //计划来源
    processingResultsOption: any = ProcessingResults; //处理结果
    processingStartsOption: any = ProcessingStarts; //处理状态

    custClassifyTypeOption: any = []
    suggestContactCycleOption: any = []
    instancyDegreeOption: any = []
    dealStatusOption: any = []
    personPage: boolean = false;
    setValue: any;

    showFlag: boolean = false;
    lastTimeContact: any;
    // labelData = 
    customerList: any;
    custNo: string;

    constructor(
        public commfunc: Commfunc,
        public cusPoolHttpService: CusPoolHttpService,
        private decimalPipe: DecimalPipe,
        public router: Router,
        public httpService1: HttpService,
        public httpService: CusOperationHttpService,
    ) {
        this.codeValues();
        this.custClassifyTypeOption = this.code['PoolCustClassifyType'];
        this.suggestContactCycleOption = this.code['PoolSuggestContactCycle'];
        this.instancyDegreeOption = this.code['PoolinstancyDegree'];
        this.dealStatusOption = this.code['PoolTaskDealStatus'];
    }

    //码值
    codeValues() {
        this.commfunc.codeValue();
        let ret = this.commfunc.codeDatas;
        return this.code = ret //code是自己声明的变量
    }

    ngOnInit() {
        debugger
        this.custId = this.inValue.custId;
        this.custName = this.inValue.custName;
        this.custClassifyType = this.inValue.custClassifyType;
        this.dealStatus = this.inValue.dealStatus;
        this.change();
        this.queryInit();
    }

    change() {
        for (let item in this.inValue) {
            if (item == 'SMZQ_005') {// 建议联系频度
                this.suggestContactCycle = this.codeValue(this.inValue['SMZQ_005'], this.suggestContactCycleOption);
            }
            if (item == 'SMZQ_006') {// 紧急程度
                this.instancyDegree = this.codeValue(this.inValue['SMZQ_006'], this.instancyDegreeOption);
            }
            if (item == 'SMZQ_007') {// 处理状态
                this.dealStatus = this.codeValue(this.inValue['SMZQ_007'], this.dealStatusOption);
            }
        }
    }

    queryInit() {
        this.first = 0;
        this.pageNum = 1;
        this.pageSize = 10;
        this.queryPorcessedCommunicationEvent();
    }

    // 查询
    queryPorcessedCommunicationEvent() {
        let param = {
            custId: this.custId,
            pageNum: this.pageNum,
            pageSize: this.pageSize,
        };
        this.httpService.queryPorcessedCommunicationEvent(param).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.tableList = data.resultList == null ? [] : data.resultList;
                this.totalNum = data.totalNum;
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务失败！" });
        })
    }

    //转码
    codeValue(val, arr: any = []) {
        let temp = "";
        if (arr.length > 0) {
            arr.forEach(item => {
                if (item['label'] == val) {
                    temp = item['value']
                }
            });
        }
        return temp
    }

    // 返回
    return() {
        this.outValue.emit(false);
    }

    // 新建客户联系回调
    addAustom(event) {
        this.personPage = event.display;
        if (event.sign && event.sign == 'cancel') {
        } else {
            this.msgs = [];
            this.msgs.push({ severity: 'success', summary: '提示', detail: "新建成功" })
        }
        this.queryInit();
    }

    // 修改原计划
    modfiyPlan() {
        this.showFlag = false;
        this.personPage = true;
    }

    // 继续新建
    carryNew() {
        this.showFlag = false;
        this.personPage = true;
        this.setValue = {
            custId: this.custNo,
            custName: this.custName,
            contactSource: "07",
            lastTimeContact: this.lastTimeContact
        }
    }

    // 客户联系
    contact() {
        this.custNo = this.custId;
        let param = {
            custId: this.custNo,
            custName: this.custName
        }
        this.httpService.checkCommunicationEventResultInfo(param).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.lastTimeContact = data.lastTimeContact;
                this.customerList = data;
                //  存在
                if (data.flag == "0") {
                    this.showFlag = true;
                    this.setValue = data;
                    this.setValue['updateFlag'] = true; // 修改标识
                    // this.setValue = {
                    //   custId: this.custNo,
                    //   // custName: this.custName,
                    //   contactSource: "03",
                    //   lastTimeContact: data.lastTimeContact

                    // }
                } else {
                    // 不存在客户联系，新建
                    this.setValue = {
                        custId: this.custNo,
                    }
                    this.personPage = true;
                    this.carryNew()
                }
                // if (data.contactSource !== "01") {
                //   this.showFlag = true;
                // } else {
                //   this.carryNew()
                // }
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务失败！" });
        })



        // this.personPage = true;
        // this.showFlag = true;
    }

    //按钮权限
    permissionCheck(id) {
        return this.commfunc.permissionCheck(id);
    }

}