    // 保存-客户信息列表
    toApplyProduct() {

        let bool = true;
        // let arr = [];
        let arrTemp = [];
        let objB = {};
        let arrC = [];
        let obj = {};

        if (this.titleUpList.length > 0) {
            debugger
            // 修改字段的值
            this.tableList.forEach((element, index) => {
                objB = {};
                obj = {};
                arrTemp = [];
                objB['custId'] = element['custId'];
                objB['marketTopic'] = element['SMZQ_004'];
                for (var i = 0; i < this.titleUpList.length; i++) {
                    if (element[this.titleUpList[i]]) {
                        obj[this.titleUpList[i]] = element[this.titleUpList[i]];
                    } else {
                        obj[this.titleUpList[i]] = '';
                    }
                }
                if (JSON.stringify(obj) != '{}') {
                    arrTemp.push(obj)
                    objB['custFiedList'] = arrTemp;
                }
                arrC.push(objB);
            });

            // 批量修改服务
            let param = {
                custInfoList: arrC,
            }
            this.cusPoolHttpService.modifyCustPoolSubjoin(param).subscribe(data => {
                if (data.returnCode.code == 'success') {
                    this.queryInit();
                    this.msgs = [];
                    this.msgs.push({ severity: 'success', summary: '提示', detail: "保存成功！" });
                } else {
                    this.msgs = [];
                    this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
                }
            }, error => {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务失败！" });
            });
        } else {
            this.isShowSave = false;
        }
    }

