import { Component, OnInit, Input } from '@angular/core';
import { FormGroup, FormBuilder, Validators } from "@angular/forms";
import { Message } from "primeng/primeng";
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
import { tableMessage } from "app/@uisftech/common/constant/config";
import { CusPoolHttpService } from 'app/pages/tzb/custom/http/custom-pool/custom-pool.http.service';

@Component({
    selector: 'big-data-plat-pool',
    templateUrl: './big-data-plat-pool.component.html',
    styleUrls: ['./big-data-plat-pool.component.scss'],
})
export class BigDataPlatPoolComponent implements OnInit {
    @Input('in-value') inValue: any;
    tabMesg: any = tableMessage;
    canupOrg: boolean = false;
    canupUser: boolean = false;
    tableList: any = [];
    processOrgId: string = null;
    processOrgName: string = null;
    processUserId: string = null;
    processUserName: string = null;
    msgs: Message[] = [];
    custClassificationOption: any = [];
    code: any;
    permission: string;
    user: any;
    user2: any;
    userId: string;
    orgId: string;
    userName: string;
    orgName: string;
    op: string;
    contactSource: string;

    orgTreeDisplay: boolean = false;
    ownerOrgInValue: any;
    userTreeDisplay: boolean = false;
    ownerInValue: any;
    idenType: any = [];

    pageNum: number = 1;
    pageSize: number = 10;
    first: number = 0;
    totalNum: number = 0;

    constructor(
        public commfunc: Commfunc,
        public cusPoolHttpService: CusPoolHttpService,
    ) {
        this.codeVal();
        this.idenType = this.code['CERT_TYPE'];
    }

    //码值
    codeVal() {
        this.commfunc.codeValue();
        let ret = this.commfunc.codeDatas;
        return this.code = ret //code是自己声明的变量
    }

    ngOnInit() {
        this.pageNum = 1;
        this.pageSize = 10;
        this.first = 0;
        this.queryList();
    }

    // 大数据推送待入池客户名单查询
    queryList() {
        let param = {
            inputParams: {
                modelNo: this.inValue.modelNo,
                inventoryStatus:'1',
                pageNum: this.pageNum,
                pageSize: this.pageSize,
            }
        }
        this.cusPoolHttpService.queryBdCustPoolPre(param).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.tableList = data.outputParams.resultList == null ? [] : data.outputParams.resultList;
                this.totalNum = data.outputParams.totalNum;
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务失败！" });
        });
    }

    //分页
    paginate(event) {
        this.pageSize = event.rows * 1;
        let currentPage = event.page + 1;
        this.pageNum = currentPage;
        this.first = (currentPage - 1) * this.pageSize;
        this.queryList();
    }

    reset() {
        if (this.canupOrg) {
            this.processOrgName = null;
            this.processOrgId = null;
        }
        if (this.canupUser) {
            this.processUserName = null;
            this.processUserId = null;
        }
    }

    // 机构树
    openTree() {
        this.orgTreeDisplay = true;
        this.ownerOrgInValue = this.orgId;
    }

    // 关闭-机构树
    contacttreeCall(event) {
        this.orgTreeDisplay = false;
        this.userTreeDisplay = false;
        this.processOrgId = event.orgId;
        this.processOrgName = event.orgName;
    }

    // 关闭模态框
    onHideClose(param) {
        let dataParam = param;
        if (dataParam == 'org') {
            this.orgTreeDisplay = false;
        }
        if (dataParam == 'user') {
            this.userTreeDisplay = false;
        }
    }

    // 选人
    openPeopleTree() {
        if (this.processOrgName == null) {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: "请先选择机构！" });
            return
        }
        this.userTreeDisplay = true;
        this.ownerInValue = this.processOrgId;
    }

    // 关闭人员模态框
    workerCall(event) {
        this.userTreeDisplay = false;
        this.processUserName = event[0].tellerName;
        this.processUserId = event[0].tellerId;
        this.processOrgName = event[0].orgName;
        this.processOrgId = event[0].orgId;
    }

    compileStr(cust) {
        return this.commfunc.compileStr(cust)
    }

    //按钮权限
    permissionCheck(id) {
        return this.commfunc.permissionCheck(id);
    }

}