/**
 * @author fanhualing 2019-6-1 全生命周期-客户成效查询
 */
import { Component, OnInit } from '@angular/core';
import { FormGroup, FormBuilder, Validators } from "@angular/forms";
import { Message } from "primeng/primeng";
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
import { ContactEffectivenessEnquiryBean } from 'app/pages/tzb/custom/life-cycle/contact-effectiveness-enquiry/bean/contact-effectiveness-enquiry.bean';
import { CALENDAR_CN } from 'app/pages/tzb/custom/credit-ccm/common/http/credit-ccm.http.constant';
import { tableMessage } from "app/@uisftech/common/constant/config";
import { CusPoolHttpService } from 'app/pages/tzb/custom/http/custom-pool/custom-pool.http.service';
import * as $ from 'jquery';
import { API } from '../../../../../@uisftech/view/http/uisftech-view.http.constant';
import { TZB_HTTP_CUS } from "app/@uisftech/common/service/http.constant";
import { UisftechCommonHttpService } from 'app/@uisftech/common/http/uisftech-common-http.service';
import { HttpService } from "app/@uisftech/common/service/http.service";

@Component({
    selector: 'contact-effectiveness-enquiry',
    templateUrl: './contact-effectiveness-enquiry.component.html',
    styleUrls: ['./contact-effectiveness-enquiry.component.scss'],
})
export class ContactEffectivenessEnquiryComponent implements OnInit {

    bean: ContactEffectivenessEnquiryBean = new ContactEffectivenessEnquiryBean();
    startDate: Date = null;
    endDate: Date = null;
    msgs: Message[];
    ch: any = CALENDAR_CN;
    tableList: any = [];
    tabMesg: any = tableMessage;
    processOrgId: string = null;
    processOrgName: string = null;
    processUserId: string = null;
    processUserName: string = null;

    orgTreeDisplay: boolean = false;
    ownerOrgInValue: any;
    userTreeDisplay: boolean = false;
    ownerInValue: any;
    permission: string;
    rzjg: string;
    user: any;
    user2: any;
    userId: string;
    orgId: string;
    userName: string;
    orgName: string;
    op: string;
    canupOrg: boolean = false;
    canupUser: boolean = false;
    totalNum: number = 0;
    first: number = 0;

    constructor(
        public commfunc: Commfunc,
        public cusPoolHttpService: CusPoolHttpService,
        public httpService1: HttpService,
    ) {

    }

    ngOnInit() {
        this.user = JSON.parse(this.commfunc.getSessionData('commonHeader'));
        this.user2 = JSON.parse(this.commfunc.getSessionDataCH('chName'));
        // 当前登录用户
        if (this.user.orgId) {
            this.orgId = this.user.orgId;
        }
        if (this.user.userId) {
            this.userId = this.user.userId;
        }
        if (this.user2.userName) {
            this.userName = this.user2.userName;
        }
        if (this.user2.orgName) {
            this.orgName = this.user2.orgName;
        }

        // 获取是否管理岗、机构层级、任职机构
        if (this.user.extParam.managePermission) {
            this.permission = this.user.extParam.managePermission;
        }
        if (this.user.extParam.operatingLevel) {
            this.op = this.user.extParam.operatingLevel; //层级
        }
        if (this.user.extParam.orgId) {
            this.rzjg = this.user.extParam.orgId;
        }
        debugger
        // 判断逻辑
        if (this.op == 'GENERAL_BANK' || this.op == 'GROUP') { // 总行/集团-默认集团
            this.processOrgId = '0000';
            this.processUserId = null;
            this.processUserName = null;
            this.canupOrg = true;
            this.canupUser = true;
        } else if (this.op == 'BRANCH') { // 分行-默认本级
            this.processOrgId = this.rzjg;
            this.processUserId = null;
            this.processUserName = null;
            this.canupOrg = true;
            this.canupUser = true;
        } else if (this.permission == 'Y') { // 支行管理岗
            this.processOrgId = this.rzjg;
            this.processUserId = null;
            this.processUserName = null;
            this.canupOrg = false;
            this.canupUser = true;
        } else { // 支行客户经理、柜员
            this.processOrgId = this.rzjg;
            this.processUserId = this.userId;
            this.processUserName = this.userName;
            this.canupOrg = false;
            this.canupUser = false;
        }
        this.query();
        this.queryInit();
    }

    queryInit() {
        this.bean.pageSize = 10;
        this.bean.pageNum = 1;
        this.first = 0;
        this.queryList();
    }

    queryList() {
        if (this.processOrgName == null) {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: '机构不能为空！' });
            return
        }
        this.bean.orgId = this.processOrgId;
        this.bean.userId = this.processUserId;
        if (this.startDate == null || this.startDate == undefined) {
            this.bean.startDataDt = undefined;
        } else {
            this.bean.startDataDt = this.commfunc.transDateN(this.startDate);
        }
        if (this.endDate == null || this.endDate == undefined) {
            this.bean.endDataDt = undefined;
        } else {
            this.bean.endDataDt = this.commfunc.transDateN(this.endDate);
        }
        console.log(this.bean);
        console.log('成效查询')
        let param = {
            inputParams: this.bean
        }
        this.cusPoolHttpService.queryCustEffect(param).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.tableList = data.outputParams.resultList == null ? [] : data.outputParams.resultList;
                this.totalNum = data.outputParams.totalNum;
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务失败！" });
        });
    }

    reset() {
        this.bean = new ContactEffectivenessEnquiryBean();
        this.bean.pageNum = 1;
        this.bean.pageSize = 10;
        this.first = 0;
        this.startDate = null;
        this.endDate = null;
        // if (this.canupOrg) {
        //     this.processOrgName = null;
        //     this.processOrgId = null;
        // }
        if (this.canupUser) {
            this.processUserName = null;
            this.processUserId = null;
        }
    }

    // 机构树
    openTree() {
        this.orgTreeDisplay = true;
        this.ownerOrgInValue = this.processOrgId;
    }

    //分页
    paginate(event) {
        this.bean.pageSize = event.rows * 1;
        let currentPage = event.page + 1;
        this.bean.pageNum = currentPage;
        this.first = (currentPage - 1) * this.bean.pageSize;
        this.queryList();
    }

    // 关闭-机构树
    contacttreeCall(event) {
        this.orgTreeDisplay = false;
        this.userTreeDisplay = false;
        this.processOrgId = event.orgId;
        this.processOrgName = event.orgName;
    }

    // 关闭模态框
    onHideClose(param) {
        let dataParam = param;
        if (dataParam == 'org') {
            this.orgTreeDisplay = false;
        }
        if (dataParam == 'user') {
            this.userTreeDisplay = false;
        }
    }

    // 选人
    openPeopleTree() {
        if (this.processOrgName == null) {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: "请先选择机构！" });
            return
        }
        this.userTreeDisplay = true;
        this.ownerInValue = this.processOrgId;
    }

    // 关闭人员模态框
    workerCall(event) {
        this.userTreeDisplay = false;
        this.processUserName = event[0].tellerName;
        this.processUserId = event[0].tellerId;
        this.processOrgName = event[0].orgName;
        this.processOrgId = event[0].orgId;
    }

    // 开始、结束日期判断
    showTime(param1, param2) {
        if (param1 == 'plan') {
            if (this.startDate && this.endDate) {
                if (this.startDate > this.endDate) {
                    this.msgs = [];
                    this.msgs.push({ severity: 'error', summary: '提示', detail: '开始日期不能大于结束日期' });
                    setTimeout(() => {
                        if (param2 == "start") {
                            this.startDate = null;
                        } else {
                            this.endDate = null;
                        }
                    })
                }
            }
        }
    }

    // 手动输入日期
    handInputDate(param1, param2) {
        this.showTime(param1, param2);
    }

    //查询
    query() {
        let queryParam = {
            orgId: this.processOrgId,
            relationshipType: '0'
        }
        let erro = '';
        let params = this.httpService1.handleParams(queryParam)

        $.ajax({
            url: TZB_HTTP_CUS + API.queryOrgByOrgIdAndRelationship,
            type: 'POST',
            data: JSON.stringify(params),
            async: false,
            headers: {
                'x-ajax': '1',
                'Content-Type': 'application/json'
            },
            dataType: 'json',
            success: (data) => {
                if (data.returnCode.code == 'success') {
                    // 经办机构
                    if (data.orgList) {
                        this.processOrgName = data.orgList[0]['orgName'];
                    }
                } else {
                    erro = data.returnCode.message;
                }
            },
            error: (err) => {
                erro = '调用服务失败';
            }
        })
        if (erro != '') {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: erro });
            return
        }
    }

    //按钮权限
    permissionCheck(id) {
        return this.commfunc.permissionCheck(id);
    }

}