import { Component, OnInit } from '@angular/core';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
import { Message } from "primeng/primeng";
import { tableMessage } from "app/@uisftech/common/constant/config";
import { ListAfterEnterThePoolBean } from 'app/pages/tzb/custom/life-cycle/list-after-enter-the-pool/bean/list-after-enter-the-pool.bean';
import { CusPoolHttpService } from 'app/pages/tzb/custom/http/custom-pool/custom-pool.http.service';
import { ActivatedRoute, Router } from '@angular/router';

@Component({
    selector: 'list-after-enter-the-pool',
    templateUrl: './list-after-enter-the-pool.component.html',
    styleUrls: ['./list-after-enter-the-pool.component.scss'],
})

export class ListAfterEnterThePoolComponent implements OnInit {

    bean: ListAfterEnterThePoolBean = new ListAfterEnterThePoolBean();
    tableMesg: any = tableMessage;
    msgs: Message[] = [];
    first: number = 0;
    totalNum: number = 0;
    tableList: any = [];
    code: any;
    idenType: any = [];
    custSource: any = [];
    marketTopic: any = [];
    instancyDegree: any = [];
    suggestContactCycle: any = [];
    dealStatus: any = [];
    inventoryStatus: any = [];
    customerAddDisplay: boolean = false;
    customerAddInvalue: any;
    sousuoListInvalue: any;
    sousuoListDisplay: boolean = false;
    dataSource: any = [];
    // 批量导入文件
    batchFileInvalue: any;
    batchFileDisplay: boolean = false;
    // 批量导入结果页面
    batchFileResultInvalue: any;
    batchFileResultDisplay: boolean = false;

    constructor(
        public commfunc: Commfunc,
        public cusPoolHttpService: CusPoolHttpService,
        public routeInfo: ActivatedRoute,
    ) {
        this.codeValues()
        this.idenType = this.code['CERT_TYPE'];
        this.custSource = this.code['PoolCustSource'];
        this.marketTopic = this.code['PoolMarketTopic'];
        this.instancyDegree = this.code['PoolinstancyDegree'];
        this.suggestContactCycle = this.code['PoolSuggestContactCycle'];
        this.dealStatus = this.code['PoolDealStatus'];
        this.inventoryStatus = this.code['PoolinventoryStatus'];
        this.dataSource = this.code['PoolDataSource'];
        // this.custSource = [
        //     { label: '请选择', value: '' },
        //     { label: '互联网获客', value: '1' },
        //     { label: '大数据推送', value: '2' },
        //     { label: '分支行制定', value: '3' },
        //     { label: '客户经理自定义', value: '4' },
        // ]
        // this.marketTopic = [
        //     { label: '请选择', value: '' },
        //     { label: '交叉销售', value: '1' },
        //     { label: '礼品登记', value: '2' },
        //     { label: '营销管理', value: '3' },
        // ];
        // this.instancyDegree = [
        //     { label: '请选择', value: '' },
        //     { label: '一星', value: '1' },
        //     { label: '二星', value: '2' },
        //     { label: '三星', value: '3' },
        //     { label: '四星', value: '4' },
        //     { label: '五星', value: '5' },
        // ]
        // this.suggestContactCycle = [
        //     { label: '请选择', value: '' },
        //     { label: '1个月', value: '01' },
        //     { label: '3个月', value: '02' },
        //     { label: '6个月', value: '03' },
        //     { label: '12个月', value: '04' },
        //     { label: '24个月', value: '05' },
        // ]
        // this.dealStatus = [
        //     { label: '请选择', value: '' },
        //     { label: '待分配', value: '4' },
        //     { label: '已分配待入池', value: '5' },
        //     { label: '已分配已入池', value: '6' },
        // ]
        // this.inventoryStatus = [
        //     { label: '请选择', value: '' },
        //     { label: '失效', value: '0' },
        //     { label: '有效', value: '1' },
        // ]
    }

    //码值
    codeValues() {
        this.commfunc.codeValue();
        let ret = this.commfunc.codeDatas;
        return this.code = ret //code是自己声明的变量
    }

    ngOnInit() {
        let aa = this.commfunc.getSessionDataCH('poolMarketingCampaignId');
        if (this.commfunc.getSessionDataCH('poolMarketingCampaignId')) {
            console.log(this.commfunc.getSessionDataCH('poolMarketingCampaignId'));
            console.log(this.commfunc.getSessionDataCH('poolMarketingCampaignIdResult'));
            this.sousuoListInvalue = {
                marketingCampaignId: this.commfunc.getSessionDataCH('poolMarketingCampaignId'),
                poolMarketingCampaignIdResult: this.commfunc.getSessionDataCH('poolMarketingCampaignIdResult'),
            }
            this.sousuoListDisplay = true;
            this.commfunc.clearSession('poolMarketingCampaignId');
            this.commfunc.clearSession('poolMarketingCampaignIdResult')
        }
        this.queryInit();
    }

    queryInit() {
        this.first = 0;
        this.bean.pageNum = 1;
        this.bean.pageSize = 10;
        this.query();
    }

    // 查询
    query() {
        this.bean.dedStatus = '0';
        let param = {
            inputParams: this.bean
        }
        this.cusPoolHttpService.queryCustPoolPre(param).subscribe(data => {
            if (data.returnCode.code == 'success') {
                if (data.outputParams) {
                    this.tableList = data.outputParams.resultList == null ? [] : data.outputParams.resultList
                    this.totalNum = data.outputParams.totalNum;
                } else {
                    this.tableList = [];
                    this.totalNum = 0;
                }
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务失败！" });
        });
    }


    // 重置
    reset() {
        this.bean = new ListAfterEnterThePoolBean();
        this.first = 0;
        this.bean.pageNum = 1;
        this.bean.pageSize = 10;
    }

    // 新增
    customerAdd() {
        this.customerAddDisplay = true;
    }

    close() {
        this.customerAddDisplay = false;
        this.sousuoListDisplay = false;
        this.batchFileDisplay = false;
        this.batchFileResultDisplay = false;
        this.queryInit();
    }

    //分页
    paginate(event) {
        this.bean.pageSize = event.rows * 1;
        let currentPage = event.page + 1;
        this.bean.pageNum = currentPage;
        this.first = (currentPage - 1) * this.bean.pageSize;
        this.query();
    }

    compileStr(cust) {
        return this.commfunc.compileStr(cust)
    }
    
    outValue(event) {
        debugger
        console.log(event)
        this.customerAddDisplay = false;
        if (event.opType && event.opType == 'bigData'){
           this.close();
           this.queryInit();
        } else{
           this.batchFileDisplay = true;
           this.batchFileInvalue = event.batchFileInvalue;
        }
     

        // this.close();
        // this.queryInit();
    }
    
    batchOutDisplayCall(event){
        debugger
        console.log(event)
        this.batchFileDisplay = false;
        if(event.batchFileResultInvalue){
            this.batchFileResultDisplay = true;
            this.batchFileResultInvalue = event.batchFileResultInvalue;
        } 
    }

    batchResulltoutDisplayCall(event) {
        debugger
        console.log(event)
        this.batchFileResultDisplay = false;
    }

    //按钮权限
    permissionCheck(id) {
        return this.commfunc.permissionCheck(id);
    }

    outDisplayCall(event) {
        this.sousuoListDisplay = false;
    }
}