import { Component, OnInit } from '@angular/core';
import { FormGroup, FormBuilder, Validators } from "@angular/forms";
import { Message } from "primeng/primeng";
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
import { tableMessage } from "app/@uisftech/common/constant/config";
import { CusPoolHttpService } from 'app/pages/tzb/custom/http/custom-pool/custom-pool.http.service';

@Component({
    selector: 'contact-progress-enquiry',
    templateUrl: './contact-progress-enquiry.component.html',
    styleUrls: ['./contact-progress-enquiry.component.scss'],
})
export class ContactProgressEnquiryComponent implements OnInit {
    tabMesg: any = tableMessage;
    canupOrg: boolean = false;
    canupUser: boolean = false;
    tableList: any = [];
    processOrgId: string = null;
    processOrgName: string = null;
    processUserId: string = null;
    processUserName: string = null;
    msgs: Message[] = [];
    custClassificationOption: any = [];
    code: any;
    permission: string;
    user: any;
    user2: any;
    userId: string;
    orgId: string;
    userName: string;
    orgName: string;
    op: string;
    contactSource: string;

    orgTreeDisplay: boolean = false;
    ownerOrgInValue: any;
    userTreeDisplay: boolean = false;
    ownerInValue: any;

    constructor(
        public commfunc: Commfunc,
        public cusPoolHttpService: CusPoolHttpService,
    ) {
        this.codeValues();
        this.custClassificationOption = this.code['PoolCustClassifyType'];
        // this.custClassificationOption = [
        //     { label: '潜在客户', value: '01' },
        //     { label: '新客户', value: '02' },
        //     { label: '老客户', value: '03' },
        //     { label: '流失客户', value: '04' }
        // ]; // 客户分类
    }

    //码值
    codeValues() {
        this.commfunc.codeValue();
        let ret = this.commfunc.codeDatas;
        return this.code = ret //code是自己声明的变量
    }

    ngOnInit() {

        this.user = JSON.parse(this.commfunc.getSessionData('commonHeader'));
        this.user2 = JSON.parse(this.commfunc.getSessionDataCH('chName'));
        // 当前登录用户
        if (this.user.orgId) {
            this.orgId = this.user.orgId;
        }
        if (this.user.userId) {
            this.userId = this.user.userId;
        }
        if (this.user2.userName) {
            this.userName = this.user2.userName;
        }
        if (this.user2.orgName) {
            this.orgName = this.user2.orgName;
        }

        // 当前登录用户岗位
        if (this.user.extParam.managePermission) {
            this.permission = this.user.extParam.managePermission;
        }
        if (this.user.extParam.operatingLevel) {
            this.op = this.user.extParam.operatingLevel; //层级
        }

        if (this.permission == 'N') { // 客户经理 + 主管
            this.canupOrg = false;
            this.canupUser = false;
            this.processOrgName = this.orgName;
            this.processUserName = this.userName;
            this.processOrgId = this.orgId;
            this.processUserId = this.userId;
        } else {
            if (this.permission == 'Y') {
                // 如果为主管、支行部门SUB_BRANCH_DT
                if (this.op == 'SUB_BRANCH_DT' || this.op == 'SUB_BRANCH') {
                    this.canupOrg = false;
                    this.canupUser = true;
                    this.processOrgName = this.orgName;
                    this.processUserName = null;
                    this.processOrgId = this.orgId;
                    this.processUserId = null;
                } else {
                    //如果为其他部门
                    this.canupOrg = true;
                    this.canupUser = true;
                    this.processOrgName = null;
                    this.processUserName = null;
                    this.processOrgId = null;
                    this.processUserId = null;
                }
            }
        }
    }

    doQuery() {
        if (this.processOrgName == null) {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: "机构不能为空！" });
            return
        }
        if (this.processUserName == null) {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: "员工不能为空！" });
            return
        }
        this.queryList();
    }

    queryList() {
        let param = {
            contactSource: '07', // 计划来源 - 07-客户分类池
            processOrgId: this.processOrgId,
            processUserId: this.processUserId,
            pageNum: 1,
            pageSize: 100,
        }
        this.cusPoolHttpService.communicationEventClassificationAnalysis(param).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.tableList = data.resultList;
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务失败！" });
        });
    }

    reset() {
        if (this.canupOrg) {
            this.processOrgName = null;
            this.processOrgId = null;
        }
        if (this.canupUser) {
            this.processUserName = null;
            this.processUserId = null;
        }
    }

    // 机构树
    openTree() {
        this.orgTreeDisplay = true;
        this.ownerOrgInValue = this.orgId;
    }

    // 关闭-机构树
    contacttreeCall(event) {
        this.orgTreeDisplay = false;
        this.userTreeDisplay = false;
        this.processOrgId = event.orgId;
        this.processOrgName = event.orgName;
    }

    // 关闭模态框
    onHideClose(param) {
        let dataParam = param;
        if (dataParam == 'org') {
            this.orgTreeDisplay = false;
        }
        if (dataParam == 'user') {
            this.userTreeDisplay = false;
        }
    }

    // 选人
    openPeopleTree() {
        if (this.processOrgName == null) {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: "请先选择机构！" });
            return
        }
        this.userTreeDisplay = true;
        this.ownerInValue = this.processOrgId;
    }

    // 关闭人员模态框
    workerCall(event) {
        this.userTreeDisplay = false;
        this.processUserName = event[0].tellerName;
        this.processUserId = event[0].tellerId;
        this.processOrgName = event[0].orgName;
        this.processOrgId = event[0].orgId;
    }


    //按钮权限
    permissionCheck(id) {
        return this.commfunc.permissionCheck(id);
    }

}